import React from 'react';
import fetch from 'isomorphic-unfetch'


const Noticia = ({noticia}) => {

    const {source, title, description, urlToImage, url} = noticia

    const verifiarImagen = async url => {        
            const solicitud = await fetch(url)
            //const imagen = await solicitud.json()
            return solicitud                   
    }

    
    const imagen = verifiarImagen(urlToImage)
        .then(Response => {return Response})
        .catch(() => {return <p>hola</p>})
    

    return (
        <div className = 'col-md-6 col-12'>
            <div className="card mb-3">                
                <img  src={imagen} alt="Card image"/>
                <div className="card-body">
                    <h5 className="card-title">{title}</h5>                    
                </div>
                <div className="card-body">
                    <p className="card-text">{description}</p>
                    <p className="card-text">{source.name}</p>
                </div>                
                <div className="card-body">
                    <button href={url} target='_blank' type="button" className="btn btn-primary btn-lg btn-block">Leer Noticia</button>
                </div>                
            </div>
            <style jsx>{`
                img {
                    height: 200px; 
                    width: 100%; 
                    display: block;
                }
            `}
            </style>
        </div>        
    );
};


export default Noticia;